@use '../base/mixins' as mixin;

@import '../blocks/_banner-section';
@import '../blocks/_investors';
@import '../blocks/_review';
@import '../blocks/_get-started';
@import '../blocks/_faq';

.home-stats {
  &__row {
    display: flex;
    align-items: stretch;

    @include mixin.phone {
      flex-direction: column;
    }
  }

  &__item {
    text-align: center;
    position: relative;
    flex-grow: 1;

    &:not(:first-child) {
      padding-left: 14px;

      @include mixin.phone {
        padding-left: 0;
        padding-top: 16px;
      }
    }

    &:not(:last-child) {
      padding-right: 14px;

      @include mixin.phone {
        padding-right: 0;
        padding-bottom: 16px;
      }

      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 1px;
        height: 100%;
        background: var(--secondary-light, #eee);

        @include mixin.phone {
          top: unset;
          right: 0;
          bottom: 0;
          height: 1px;
          width: 100%;
        }
      }
    }
  }

  .h3-custom {
    margin-bottom: 8px;

    @include mixin.phone {
      margin-bottom: 4px;
    }
  }

  p {
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;

    @include mixin.phone {
      font-size: 14px;
    }
  }
}

.home-table {
  .table {
    overflow: hidden;
    margin: 0;

    &--desktop {
      @include mixin.tablet {
        display: none;
      }
    }

    &--mobile {
      display: none;

      @include mixin.tablet {
        display: block;
        min-width: 1800px;
        text-align: left;
      }
    }

    td,
    th {
      position: relative;
      border-top: none;
      border-bottom: none;
      padding: 16px;

      &:first-child {
        padding: 8px;
      }

      &:last-child {
        padding: 8px;
      }
    }

    &__head {
      border-bottom: 1px solid var(--secondary-light, #EEE);
    }

    &__row {

      &:not(:last-child) {

        td,
        th {
          border-bottom: 1px solid var(--secondary-light, #EEE);
        }
      }

      &:first-child {
        td,
        th {
          border-top-left-radius: 8px;
          border-top-right-radius: 8px;
        }
      }

      &:last-child {
        td,
        th {
          border-bottom-left-radius: 8px;
          border-bottom-right-radius: 8px;
        }
      }

      td:not(:first-child):hover::after,
      th:not(:first-child):hover::after {
        background: var(--primary-lightest, #ecf6ff);
        content: '\00a0';
        height: 10000px;
        left: 0;
        position: absolute;
        top: -5000px;
        width: 100%;
        z-index: -1;

        @include mixin.tablet {
          background: transparent !important;
        }
      }

      @include mixin.tablet {
        &:hover:not(:first-child) {

          td,
          th {
            border-bottom: 1px solid transparent;
            background: var(--primary-lightest, #ecf6ff);
          }

          td:first-child,
          th:first-child {
            border-top-left-radius: 12px;
            border-bottom-left-radius: 12px;
          }

          td:last-child,
          th:last-child {
            border-top-right-radius: 12px;
            border-bottom-right-radius: 12px;
          }
        }

        &:has(+ :hover) {

          td,
          th {
            border-bottom: 1px solid transparent;
          }
        }
      }
    }

    &__th,
    &__td {
      text-align: center;
      font-size: 14px;
      vertical-align: middle;

      a {
        font-weight: 500;
        line-height: 1.5;
        color: var(--primary-darker, #1874c1);

        span {
          font-size: 14px;

          @include mixin.small-laptop {
            font-size: 12px;
          }
        }

        svg {
          fill: var(--primary-darker, #1874c1);

          @include mixin.small-laptop {
            width: 12px;
            height: 12px;
          }
        }
      }
    }

    &__th {
      font-size: 16px;
      font-weight: 600;
      line-height: 1.2;
    }

    &--mobile .table__th,
    &--mobile .table__td {
      font-size: 12px;
      text-align: left;
      padding: 8px;
      min-width: 80px;
    }

    &--mobile .table__row:first-child {
      font-weight: 600;
    }

    &__td--first {
      font-size: 14px;
      font-weight: 600;
      line-height: 1.2;
      text-align: left;
      min-width: 182.86px;

      @include mixin.small-laptop {
        min-width: 100px;
      }
    }

    &__desc {
      font-size: 16px;
      font-weight: 400;
      line-height: 1.5;
      color: var(--secondary-darker, #6a6a6a);

      @include mixin.small-laptop {
        display: none;
      }
    }
  }

  .table-wrap {
    overflow-y: hidden;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }
}

.home-reviews {
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 48px;

    @include mixin.small-laptop {
      gap: 24px;
    }
  }

  &__btn {
    min-width: 200px;

    @include mixin.phone {
      min-width: 150px;
    }
  }
}