@use '../base/mixins' as mixin;

.faq {
  &__head {
    margin: 0 auto;
    margin-bottom: 48px;
    text-align: center;
    max-width: 499px;

    @include mixin.small-laptop {
      margin-bottom: 24px;
    }

    .h2-custom {
      margin-bottom: 12px;

      @include mixin.small-laptop {
        margin-bottom: 8px;
      }
    }
  }

  &__list {
    max-width: 842px;
    margin: 0 auto;
  }

  &__item {
    width: 100%;
    border-top: 1px solid var(--secondary-light, #EEE);

    &:last-child {
      border-bottom: 1px solid var(--secondary-light, #EEE);
    }
  }

  &__item-head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    padding: 16px 0;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.3;
    cursor: pointer;

    @include mixin.small-laptop {
      font-size: 16px;
      line-height: 1.2;
    }

    button {
      width: 21px;
      height: 21px;
      outline: none;
      padding: 0;
      transform-origin: 50% 50%;
      transform: rotateZ(45deg);
      transition: 0.3s linear;
      display: flex;
      align-items: center;
      justify-content: center;

      &:focus,
      &:active {
        border: none;
        outline: none;
        box-shadow: none;
      }

      svg {
        display: block;
      }

      path {
        fill: var(--secondary-darkest, #000);
        transition: 0.3s linear;
      }
    }

    &.collapsed {
      button {
        transform: rotateZ(0deg);

        path {
          fill: var(--secondary-darker, #6a6a6a);
        }
      }
    }
  }

  &__item-content {
    padding-bottom: 16px;

    @include mixin.small-laptop {
      font-size: 12px;
    }
  }
}