@use '../base/mixins' as mixin;

.get-started {
  &__container {
    box-sizing: border-box;
    padding: 48px 40px;
    border-radius: 32px;
    background: linear-gradient(225deg, #1e9fe8 0%, #1874c1 100%);

    @include mixin.small-laptop {
      padding: 32px;
    }

    @include mixin.phone {
      padding: 24px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: var(--secondary-lightest, #fff);

    .h2-custom {
      margin-bottom: 16px;
    }

    .subtitle {
      margin-bottom: 32px;
    }
  }

  &__img {
    margin-bottom: 16px;
  }

  &__btn {
    min-width: 200px;

    @include mixin.phone {
      min-width: 150px;
    }
  }
}